import { useTranslation } from "react-i18next";
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { useCommonScreensMediaQuery } from "utils/hooks/useCommonScreensMediaQuery";
import { useMediaQuery } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    desktopRoot: {
      position: "absolute",
      marginLeft: "48rem"
    },
    phoneRoot: {
      position: "absolute",
      marginTop: "2.8rem",
      marginLeft: "18rem"
    },
    iconImage: {
      marginTop: "-5rem"
    }
  }),
);

function ArrowRightIcon() {
  const { t } = useTranslation();
  const classes = useStyles();
  const { phoneMatch } = useCommonScreensMediaQuery();
  const matches = useMediaQuery("(min-width: 1200px)");

  return (
    <>
      {
        !matches && !phoneMatch ? <></> : <>
          {phoneMatch ?
            <div className={classes.phoneRoot}>
              <img src="/phone_arrow_left.svg" className={classes.iconImage} alt="phone_arrow_left" />
            </div> :
            <div className={classes.desktopRoot}>
              <img src="/left_arrow.svg" alt="arrow_left" className={classes.iconImage} />
            </div>}
        </>
      }
    </>
  );
}

export default ArrowRightIcon;