import { useCallback, useState } from 'react';
import { Box, Grid, MenuItem, CircularProgress } from "@material-ui/core";

import Input from "components/atoms/input/Input";
import Button from "components/atoms/button/Button";
import FormControl from "components/molecules/form-control/FormControl";
import Select from "components/atoms/select/Select";
import { ResponsiveTypography } from "components/atoms/ResponsiveTypography";
import { useTranslation } from "react-i18next";

import FormFooterText from './FormFooterText';
import FeedbackModal from '../../FeedbackModal';

function ContactUsForm() {
  const { t } = useTranslation();
  const [email, setEmail] = useState('');
  const [messageSubject, setMessageSubject] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [message, setMessage] = useState('');
  const [aboutId, setAboutId] = useState('');
  const [loading, setLoading] = useState(false);
  const [openConfirm, setOpenConfirm] = useState(false);

  const handleChange = useCallback((e, name) => {
    if (name === 'email') {
      setEmail(e.target.value);
    }
    if (name === 'messageSubject') {
      setMessageSubject(e.target.value);
    }
    if (name === 'phoneNumber') {
      setPhoneNumber(e.target.value);
    }
    if (name === 'message') {
      setMessage(e.target.value);
    }
    if (name === 'aboutId') {
      setAboutId(e.target.value);
    }
  }, []);

  const clearFormData = () => {
    setEmail('');
    setMessageSubject('');
    setPhoneNumber('');
    setMessage('');
    setAboutId('');
  };

  const onSubmit = useCallback(() => {
    setLoading(true);
    fetch('api/form', {
      method: 'post',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        messageSubject,
        phone: phoneNumber,
        email,
        message,
        about: aboutId
      }),
    })
      .then(res => res.json())
      .then(() => {
        clearFormData();
        setOpenConfirm(true);
      })
      .catch(err => {
        console.log(err);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [email, messageSubject, phoneNumber, message, aboutId]);

  return (
    <div>
      <ResponsiveTypography
        sizes={[18, 18, 16]}
        className="mg mg-b-32 typo-weight-normal"
      >
        {t("pages.contactUs.form.title")}
      </ResponsiveTypography>
      <form>
        <Grid container>
          <Grid item xs={12} sm={6} md={6} className="contact-us-form-grid-item">
            <FormControl
              label="This is about"
              labelVariant="caption"
              input={
                <Select fullWidth={true} onChange={ e => handleChange(e, 'aboutId')}>
                  <MenuItem value={1}>Ten</MenuItem>
                  <MenuItem value={2}>Twenty</MenuItem>
                  <MenuItem value={3}>Thirty</MenuItem>
                </Select>
              }
            />
          </Grid>
          <Grid item xs={12} sm={6} md={6} className="contact-us-form-grid-item">
            <FormControl
              label={t("pages.contactUs.form.emailAddress")}
              labelVariant="caption"
              required={true}
              input={<Input type="text" fullWidth={true} onChange={ e => handleChange(e, 'email')} />}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={6} className="contact-us-form-grid-item">
            <FormControl
              label={t("pages.contactUs.form.messageSubject")}
              labelVariant="caption"
              input={<Input type="text" fullWidth={true} onChange={ e => handleChange(e, 'messageSubject')} />}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={6} className="contact-us-form-grid-item">
            <FormControl
              label={t("pages.contactUs.form.phoneNumber")}
              labelVariant="caption"
              input={<Input type="text" fullWidth={true} onChange={ e => handleChange(e, 'phoneNumber')} />}
            />
          </Grid>
        </Grid>
        <Grid container>
          <Grid item xs={12} sm={12} md={8}>
            <FormControl
              label={t("pages.contactUs.form.message")}
              labelVariant="caption"
              input={
                <Input type="text" fullWidth={true} multiline={true} rows={4} onChange={ e => handleChange(e, 'message')} />
              }
            />
            </Grid>
            <Grid item xs={12} sm={12} md={4}>
              <Box className="submit-btn-container desktop">
                <Button
                  variant="contained"
                  color="primary"
                  size="large"
                  style={{ height: 56, width: 240 }}
                  onClick={onSubmit}
                >
                  {loading && <CircularProgress color="secondary" size={20} className="loader" />}
                  {t("pages.contactUs.form.submit")}
                </Button>
              </Box>
            </Grid>
        </Grid>
        <FormFooterText />
        <Box className="submit-btn-container mobile">
          <Button
            variant="contained"
            color="primary"
            size="large"
            style={{ height: 56, width: "100%", marginTop: 32 }}
            onClick={onSubmit}
          >
            {loading && <CircularProgress color="secondary" size={20} className="loader" />}
            {t("pages.contactUs.form.submit")}
          </Button>
        </Box>
      </form>
      <FeedbackModal open={openConfirm} onClose={() => setOpenConfirm(false)} />
    </div>
  );
}

export default ContactUsForm;
