import { useTranslation } from "react-i18next";
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { useCommonScreensMediaQuery } from "utils/hooks/useCommonScreensMediaQuery";
import { ResponsiveTypography } from "components/atoms/ResponsiveTypography";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    desktopRoot: {
      textAlign: "center",
      maxWidth: "43%",
      margin: "4rem auto 0"
    },
    phoneRoot: {
      textAlign: "center",
      marginBottom: "1rem"
    },
    desktopContent: {
      color: "#727171"
    },
    phoneContent: {
      maxWidth: "90%",
      color: "#727171",
      margin: "2rem auto 0"
    }
  }),
);

function PartnerBuyTextPanel() {
  const { t } = useTranslation();
  const classes = useStyles();
  const { phoneMatch } = useCommonScreensMediaQuery();

  return (
    <section className={phoneMatch ? classes.phoneRoot : classes.desktopRoot}>
      <ResponsiveTypography
        sizes={[36, 36, 24]}
        className="typo typo-weight-bold color color-black"
        variant="h2"
      >
        {t("pages.partner.buy.title")}
      </ResponsiveTypography>

      <div className={phoneMatch ? classes.phoneContent : classes.phoneContent}>
        <ResponsiveTypography className="typo typo-content" sizes={[16, 16, 16]} variant="body1">
          <span style={{ fontWeight: 400 }}>{t("pages.partner.buy.content")} </span>
        </ResponsiveTypography>
      </div>
    </section>
  );
}

export default PartnerBuyTextPanel;
